



















.page-loadmore {}

.page-loadmore__desc {

    text-align: center;

    color: #666;

    padding-bottom: 5px
}

.page-loadmore__desc:last-of-type {

    border-bottom: solid 1px #eee
}

.page-loadmore__listitem {

    height: 50px;

    line-height: 50px;

    border-bottom: solid 1px #eee;

    text-align: center
}

.page-loadmore__listitem:first-child {

    border-top: solid 1px #eee
}

.page-loadmore__wrapper {

    overflow: scroll
}

.toon-loadmore-bottom span {

    display: inline-block;

    -webkit-transition: .2s linear;

    transition: .2s linear;

    vertical-align: middle
}

.toon-loadmore-bottom span.is-rotate {

        -webkit-transform: rotate(180deg);

        transform: rotate(180deg)
}



